import React, { Component } from 'react';
import { Button, Checkbox } from 'antd';

/*
  roleLists=[
      {"roleId": "079d6f4d2fe215cf2ede1d481b7ed8c1", "roleName": "超级管理员"},
      {"roleId": "079d6f4d2fe215cf2ede1d481b7e58c1", "roleName": "超级管理员"},
  ]  
*/

export default class SetRoleModal extends Component {
    constructor(props){
        super(props);
        this.state = {
            checkedValues:[],
        }
    }
    componentDidMount(){
        const checkedValues = this.props.recordInfo.userRole?this.props.recordInfo.userRole.map(item => item.roleId):[];
        this.setState({checkedValues})
    }
    onChange = (checkedValues) => {
        this.setState({checkedValues})
    }
    handleClick = () => {
        this.props.handleOK(this.state.checkedValues);
    }
    handleCancle = () => {
        this.props.handleCancle();
    }
    render() {
        const roleList = this.props.roleLists || [];
        if(roleList.length === 0){
            return "加载角色列表失败"
        }else{
            return (
                <div style={{height:"300px"}}>
                    <div style={{height:240,overflow:"auto"}}>
                        <Checkbox.Group onChange={this.onChange} value={this.state.checkedValues}>
                            {roleList.map(item => <div key={item.roleId} style={{paddingLeft:"50px",marginBottom:8}}><Checkbox value={item.roleId}>{item.roleName}</Checkbox></div>)}
                        </Checkbox.Group>
                    </div>
                    <div style={{height:40,textAlign:"center"}}>
                        <Button type="primary" style={{margin:"0px 10px"}} onClick={this.handleClick}>确 认</Button>
                        <Button onClick={this.handleCancle}>取 消</Button>
                    </div>
                </div>
            )
        }
    }
}
