import React, { Component } from 'react'
import { Select, TreeSelect, Input, Button} from 'antd';
import styles from './index.module.less';
import { postAction } from '@/axios'

const statusOption = [
    {label:"全部",value:""},
    {label:"启用",value:"1"},
    {label:"禁用",value:"2"},
]
const RoleTypeOption = [
    {label:"全部",value:""},
    {label:"是",value:"0"},
    {label:"否",value:"1"},
]
export default class Filterbox extends Component {
    constructor(props){
        super(props);
        this.state = {
            user:"",//姓名
            // userType:"",//职务,
            // userTypeList:[
            //     // {label:"全部",value:""},
            //     // {label:"校领导",value:"1"},
            //     // {label:"教职工",value:"2"}
            // ],
            status:"",
            departMent:"",
            departmentList:[
                // {
                //     departId:"9901",
                //     departName:"职能处室",
                //     children:[
                //         {departId:"99011",departName:"院办"},
                //         {departId:"99012",departName:"教务处"}
                //     ]
                // },{
                //     departId:"9902",
                //     departName:"二级学院"
                // }
            ],
            setRoleType:"",
            pagination:{}
        }
    }
    componentDidMount(){
        // this.getPositionList();//获取职务列表
        this.getDepartmentList();//获取部门列表
        this.handleSearch();//默认执行一次搜索
        document.addEventListener("keydown",this._onkeydown);
    }
    // getPositionList = () => {
    //     postAction("/ybt/system/setting/getPositionList").then(res => {
    //         if(res.success){
    //             this.setState({
    //                 userTypeList:[{label:"全部",value:""}].concat(res.obj.map(item => ({label:item.positionName,value:item.positionId})))
    //             })
    //         }
    //     })
    // }
    _onkeydown = (e) => {
        if(e.keyCode === 13){
            this.handleSearch();
        }
    }
    componentWillUnmount(){
        document.removeEventListener("keydown",this._onkeydown)
    }
    getDepartmentList = () => {
        postAction("/ybt/system/setting/depart/getDepartList").then(res => {
            if(res.success){
                this.setState({
                    departmentList:res.obj
                })
            }
        })
    }
    //渲染部门树形菜单
    renderSelectTree = (departmentList = []) => departmentList.map(item => <TreeSelect.TreeNode key={item.departId} value={item.departId} title={item.departName}>{this.renderSelectTree(item.children)}</TreeSelect.TreeNode>);
    //选择部门
    handleChangeDepartment = (departMent) => {this.setState({departMent})}
    //修改姓名/工号
    handleChangeInput = (e) => {this.setState({user:e.target.value})}
    //修改其他下拉框
    handleChangeSelect = (val,type) => {this.setState({[type]:val})}
    //搜索
    handleSearch = (name) => {
        let filterValue = {
            keyword:this.state.user,
            // positions:this.state.userType,
            status:this.state.status,
            departs:this.state.departMent,
            hasRole:this.state.setRoleType
        }
        if(name && name === "reset"){
            this.props.handleSearch(filterValue,"reset")
        }else{
            this.props.handleSearch(filterValue)
        }
        
    }
    //重置
    handleReset=()=>{
        this.setState({ 
            user:'',
            status:'',
            departMent:'',
            setRoleType:'',
        },()=>{
            this.handleSearch('reset');
        });
    }
    render() {
        return (
            <div className={styles.filterBox}>
                <div className={styles.filterItem}>
                    姓名/工号：
                    <Input style={{width:220}} value={this.state.user} placeholder="请输入用户姓名/工号" onChange={this.handleChangeInput}/>
                </div>
                {/* <div className={styles.filterItem}>
                    职务：
                    <Select style={{width:120}} value={this.state.userType} onChange={(val) => {this.handleChangeSelect(val,"userType")}}>
                        {this.state.userTypeList.map((item) => (<Select.Option key={item.value} value={item.value}>{item.label}</Select.Option>))}
                    </Select>
                </div> */}
                <div className={styles.filterItem}>
                    状态：
                    <Select style={{width:120}} value={this.state.status} onChange={(val) => {this.handleChangeSelect(val,"status")}}>
                        {statusOption.map((item) => (<Select.Option key={item.value} value={item.value}>{item.label}</Select.Option>))}
                    </Select>
                </div>
                <div className={styles.filterItem}>
                    部门：
                    <TreeSelect
                        style={{ width: 300 }}
                        value={this.state.departMent}
                        dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                        placeholder="请选择部门"
                        allowClear
                        showSearch
                        treeNodeFilterProp="title"
                        treeDefaultExpandAll
                        onChange={this.handleChangeDepartment}
                    >
                        {this.renderSelectTree(this.state.departmentList)}
                    </TreeSelect>
                    
                </div>
                <div className={styles.filterItem}>
                    是否角色设置：
                    <Select style={{width:120}} value={this.state.setRoleType} onChange={(val) => {this.handleChangeSelect(val,"setRoleType")}}>
                        {RoleTypeOption.map((item) => (<Select.Option key={item.value} value={item.value}>{item.label}</Select.Option>))}
                    </Select>
                </div>
                <div className={styles.filterItem}>
                    <Button type='primary' ghost icon="search" onClick={this.handleSearch}>搜索</Button>
                    <Button icon="sync" style={{marginLeft:12}} onClick={this.handleReset}>重置</Button>
                </div>
            </div>
        )
    }
}
