import React, { Component } from 'react';
import { Button, Table, Switch ,Icon, message, Modal, Popconfirm} from 'antd';
import moment from 'moment';
import FilterBox from './filterbox';
import SetRoleModal from './setRoleModal';
import EditModal from './editModal';
import styles from './index.module.less';
import { postAction, getAction } from '@/axios';
import SystemTabs from '../common/systemTabs';
import {hasButtonPermission} from '@/util'
const MyIcon = Icon.createFromIconfontCN({
    scriptUrl: '//at.alicdn.com/t/font_1331187_no4dbj3r2x.js', // 在 iconfont.cn 上生成
});

export default class userManagement extends Component {
    constructor(props){
        super(props);
        this.state = {
            authorizeMode:"single",//授权角色模式
            selectedRows:[],//选择的行
            showSetRoleModal:false,//角色设置弹窗
            showDetalModal:false,//查看详情弹窗
            showEditModal:false,//编辑/新增弹窗
            recordInfo:{},//当前操作行信息
            columns:[
                {
                    title:"序号",
                    dataIndex:"sortIndex",
                    width:80,
                    fixed:"left"
                },{
                    title:"工号",
                    dataIndex:"userCode",
                    width:140
                },{
                    title:"姓名",
                    dataIndex:"name",
                    width:100
                },{
                    title:"部门",
                    dataIndex:"departName",
                    width:120
                },
                // {
                //     title:"职务",
                //     dataIndex:"positionName",
                //     width:120
                // },
                {
                    title:"手机号",
                    dataIndex:"phone",
                    width:140
                },{
                    title:"邮箱",
                    dataIndex:"email",
                    width:180
                },{
                    title:"角色",
                    dataIndex:"userRole",
                    width:120,
                    render:(text) => text.map(item => item.roleName).join(",")
                },{
                    title:"添加时间",
                    dataIndex:"createTime",
                    width:180,
                    render:text=>moment.unix(text/1000).format("YYYY/MM/DD HH:mm:ss")
                },{
                    title:"最后修改时间",
                    dataIndex:"updateTime",
                    width:180,
                    render:text=>moment.unix(text/1000).format("YYYY/MM/DD HH:mm:ss")
                },{
                    title:"修改人",
                    dataIndex:"updateBy",
                    width:100
                },{
                    title:"状态",
                    dataIndex:"status",
                    width:80,
                    fixed:"right",
                    render:(text,record) => hasButtonPermission('user004')?<Switch checkedChildren="启用" unCheckedChildren="禁用" checked={text===1?true:false} onChange={(val,e) =>{this.handleToggle(e,val,record,"single")}} />:null
                },{
                    title:"操作",
                    dataIndex:"",
                    width:140,
                    fixed:"right",
                    render:(text,record) => {
                        const disabledStyle = {color:"rgba(0,0,0,0.65)",cursor:"not-allowed",margin:"0 3px"};
                        const normalStyle = {color:"#24695C",cursor:"pointer",margin:"0 3px"}
                        return (
                            <React.Fragment>
                                {
                                    hasButtonPermission('user002') &&
                                    <MyIcon type="icon-jiaoseguanli" title="角色授权" style={normalStyle} onClick={(e)=>{this.handleAuthorize(e,record,"single")}}/>

                                }
                                {
                                    hasButtonPermission('user006') && 
                                    <Icon type="form"  title="编辑" style={record.status===1?disabledStyle:normalStyle} onClick={(e)=>{this.handleEdit(e,record)}}/>

                                }
                                
                                {
                                    record.status===1 && hasButtonPermission('user005')?
                                    <MyIcon type="icon-shanchu" title="删除" style={disabledStyle}/>:
                                    <Popconfirm
                                        title="确定删除吗？"
                                        onConfirm={(e)=>{this.handleDelete(e,record,"single")}}
                                        okText="确定"
                                        cancelText="取消"
                                    >
                                        <MyIcon type="icon-shanchu" title="删除" style={normalStyle}/>
                                    </Popconfirm>
                                }
                                {
                                    hasButtonPermission('user003') && 
                                        <Popconfirm
                                        title="确定重置密码吗？"
                                        onConfirm={(e)=>{this.handleReset(e,record,"single")}}
                                        okText="确定"
                                        cancelText="取消"
                                    >
                                        <MyIcon type="icon-zhongzhimima" title="重置密码" style={normalStyle} />
                                    </Popconfirm>
                                }
                               
                                
                            </React.Fragment>
                        )
                    }
                }
            ],
            dataSource:[
                // {userId:"001",userCode:"8765676",name:"邵明",departId:"001",departName:"党建处",positionId:"123",positionName:"副校长",phone:"13012345678",email:"13012345678@qq.com",role:"",createTime:"2018-01-11 19:00:00",status:1,updateTime:"2018-01-11 19:00:00",updateBy:"李长民",userRole:[{roleId: "6a31eeac521c88a10eb1f5cdbc097699", roleName: "教学主任"}]}
            ],
            pageInfo:{
                current:1,
                pageSize:10,
                total:0,
                showTotal: (total) => `共${total}条`,
                showQuickJumper: true // 显示页码快速跳转
            },
            roleLists:[],//角色设置弹窗界面用
            disibleButton:false,//同步按钮禁用
        }
    }
    componentDidMount(){
        //获取角色列表信息
        postAction("/ybt/system/setting/role/getRoleList").then(res => {
            if(res.success){
                this.setState({roleLists:res.obj})
            }
        });
    }
    //角色设置授权
    handleAuthorize = (e,record={},mode) => {
        if(mode){//单选模式
            this.setState({recordInfo:record,showSetRoleModal:true,authorizeMode:mode})
        }else{//批量多选模式
            if(this.state.selectedRows.length===0){
                message.info("请先选择需要修改角色的用户");
                return false;
            }
            this.setState({recordInfo:record,showSetRoleModal:true,authorizeMode:""})
        }
    }
    //修改用户角色弹窗确认按钮操作
    handleSetRoleType = (checkKeys) => {
        // console.log(checkKeys);
        if(this.state.authorizeMode==="single"){//单选模式
            const params = {
                roleIds:checkKeys,
                userIds:[this.state.recordInfo.userId]
            }
            this.setRoleType(params);
        }else{//多选模式
            const params = {
                roleIds:checkKeys,
                userIds:this.state.selectedRows.map(item => item.userId)
            }
            this.setRoleType(params);
        }
        this.setState({showSetRoleModal:false,recordInfo:{}});
    }
    //角色设置---请求
    setRoleType = (params) => {
        postAction("/ybt/system/setting/user/createUserRole",params).then(res => {
            if(res.success){
                message.success("角色设置成功");
                this.getUserList();
                this.setState({selectedRows:[]})
            }else{
                message.error(res.obj)
            }
        })
    }
    //取消修改用户角色
    handleCancleSetRoleType = () => {
        this.setState({showSetRoleModal:false,recordInfo:{}})
    }
    //重置密码
    handleReset = (e,record={},mode) => {
        e.stopPropagation();
        let params = {};
        if(mode==="single"){//单选模式
            params = { userIds:[record.userId] }
        }else{//多选模式
            if(this.state.selectedRows.length===0){
                message.info("请先选择需要重置密码的用户");
                return false;
            }
            params = { userIds:this.state.selectedRows.map(item => item.userId) }
        }
        this.resetUserPassword(params)
    }
    //重置密码--请求
    resetUserPassword = (params ={}) => {
        postAction("/ybt/system/setting/user/resetUserPassword",params).then(res => {
            if(res.success){
                message.success("重置成功")
            }else{
                message.error(res.obj)
            }
        })
    }
    //切换启用/禁用状态
    handleToggle = (e,val,record,mode) => {
        e.stopPropagation();
        if(mode){//单选模式
            // console.log(val,record)
            const params = {
                status:val?1:2,
                userIds:[record.userId]
            }
            this.updateBasicInfo(params)
        }else{//多选模式
            if(this.state.selectedRows.length===0){
                message.info("请先选择需要修改的用户");
                return false;
            }
            const params = {
                status:val?1:2,
                userIds:this.state.selectedRows.map(item => item.userId)
            }
            this.updateBasicInfo(params)
        }
    }
    //删除
    handleDelete = (e,record={},mode) => {
        e.stopPropagation();
        if(mode){//单选模式
            const params = {
                delFlag :1,
                userIds:[record.userId]
            }
            this.updateBasicInfo(params)
        }else{//多选模式
            if(this.state.selectedRows.length===0){
                message.info("请先选择需要删除的用户");
                return false;
            }
            const result = this.state.selectedRows.some(item => item.status===1);
            if(result){
                message.info("有用户为启用状态，不可删除");
                return false
            }
            const params = {
                delFlag :1,
                userIds:this.state.selectedRows.map(item => item.userId)
            }
            this.updateBasicInfo(params)
        }
    }
    //启用/禁用/删除-----请求
    updateBasicInfo = (params) => {
        postAction("/ybt/system/setting/user/updateBatchUserInfo",{...params}).then(res => {
            // console.log(res);
            if(res.success){
                message.success("操作成功");
                this.getUserList();
                this.setState({
                    selectedRows:[]
                },()=>{
                    // console.log(this.state.selectedRows)
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    //编辑
    handleEdit = (e,record) => {
        if(!record){
            this.setState({recordInfo:{},showEditModal:true});
            return false
        }
        if(record.status===1){ return false }
        this.setState({recordInfo:record,showEditModal:true})
    }
    //查看详情
    // handleViewDetail = (e,record) => {
    //     console.log("查看详情")
    // }
    
    //更新搜索条件
    updateSearchValue = (filterValue,name) => {
        if(name && name === "reset"){
            //重置分页
            let pageInfo =this.state.pageInfo;
            pageInfo.current = 1;
            pageInfo.pageSize = 10;
            this.setState({filterValue,pageInfo},()=>{
                this.getUserList();//获取用户列表
            });
        }else{
            this.setState({filterValue},()=>{
                this.getUserList();//获取用户列表
            })
        }
        
    }
    //获取用户列表--请求
    getUserList = (type) => {
        const params = {
            ...this.state.filterValue
        }
        
        let pageNum = 1;
        if(type==='page'){//翻页状态传page，其他状态不传，默认请求第一页
            pageNum = this.state.pageInfo.current;
        }
        const pageSize = this.state.pageInfo.pageSize;
        postAction(`/ybt/system/setting/user/getSysUserList?pageNum=${pageNum}&pageSize=${pageSize}`,params).then(res => {
            if(res.success){
                this.setState({
                    dataSource:res.obj.rows.map((item,index) => ({...item,sortIndex:index+1})),
                    pageInfo:{
                        current: res.obj.page, 
                        pageSize: res.obj.pageSize, 
                        total: res.obj.total, 
                        showTotal: (total) => `共${total}条数据`, 
                        showQuickJumper: true
                    }
                })
            }
        })
    }
    
    //编辑/新建用户
    handleUpdate = (info) => {
        if(JSON.stringify(this.state.recordInfo)==="{}"){
            // message.info("新增成功");
            this.createSysUser(info);
        }else{
            // message.info("修改成功")
            this.updateUserInfo(info)
        }
        this.setState({showEditModal:false,record:{}})
    }
    //添加用户---请求
    createSysUser = (params) => {
        postAction("/ybt/system/setting/user/createSysUser",params).then(res => {
            if(res.success){
                message.info("新增成功");
                this.getUserList()
            }else{
                message.error(res.obj)
            }
        })
    }
    //编辑用户---请求
    updateUserInfo = (params) => {
        // console.log(params)
        postAction("/ybt/system/setting/user/updateUserInfo",params).then(res => {
            if(res.success){
                message.info("修改成功");
                this.getUserList();
            }else{
                message.error(res.obj)
            }
        })
    }
    //取消新增/修改用户
    handleCancleEdit = () => {
        this.setState({showEditModal:false,recordInfo:{}})
    }
    changePageInfo = (pageInfo) => {
        this.setState({
            pageInfo:{
                ...this.state.pageInfo,
                current:pageInfo.current
            }
        },()=>{this.getUserList("page")})
    }
    //同步用户及部门
    handleSynchronize = () => {
        if(this.state.disibleButton){
            console.log("无效");
            return false
        }
        this.setState({disibleButton:true})
        //发送同步请求
        getAction("/ybt/system/setting/sync/userDepart").then(res=>{
            if(res.success){
                message.success("同步成功");
                this.setState({disibleButton:false})
                this.getUserList();
            }else{
                this.setState({disibleButton:false})
            }
        })
        // this.getUserList();
    }
    render() {
        const rowSelection = {
            onChange: (selectedRowKeys, selectedRows) => {
                console.log(selectedRowKeys)
               this.setState({selectedRows})
            },
            selectedRowKeys:this.state.selectedRows.map(item=>item.userId)
        }
        return (
            <React.Fragment>
            <SystemTabs activeTab="userManagement"/>
            <div className={styles.container}>
                <div className={styles.card}>
                    {/* <div className={styles.cardTitle}>用户管理</div> */}
                    <FilterBox handleSearch={(filterValue,name)=>{this.updateSearchValue(filterValue,name)}}/>
                    <div className={styles.operation}>
                        {
                            hasButtonPermission('user001') && 
                            <Button icon="plus" type="primary" onClick={this.handleEdit}>添加用户</Button>

                        }
                        {
                            hasButtonPermission('user002') && 
                            <Button onClick={this.handleAuthorize}>角色设置</Button>

                        }
                        {
                            hasButtonPermission('user003') &&
                            <Button onClick={this.handleReset}>重置密码</Button>

                        }
                        {
                            hasButtonPermission('user004') && <React.Fragment>
                            <Button onClick={(e) =>{this.handleToggle(e,false)}}>禁 用</Button>
                             <Button onClick={(e) =>{this.handleToggle(e,true)}}>启 用</Button>
                            </React.Fragment>
                        }
                        {
                            hasButtonPermission('user005') && 
                            <Button onClick={this.handleDelete}>删 除</Button>

                        }
                        {
                            hasButtonPermission('user007') && 
                            <Button onClick={this.handleSynchronize}>同步用户及部门</Button>
                        }
                    </div>
                    <Table
                        rowKey="userId"
                        columns={this.state.columns}
                        dataSource={this.state.dataSource}
                        rowSelection={rowSelection}
                        scroll={{x:1460}}
                        pagination={this.state.pageInfo}
                        onChange={this.changePageInfo}
                        // rowClassName={(record) => record.sortIndex === this.state.recordInfo.sortIndex?styles.selectRow:""}
                    />
                </div>
                <Modal
                    visible={this.state.showSetRoleModal}
                    title={"角色设置"}
                    width={320}
                    onCancel={this.handleCancleSetRoleType}
                    footer={false}
                    destroyOnClose={true}
                    bodyStyle={{paddingBottom:0}}
                >
                    <SetRoleModal recordInfo={this.state.recordInfo} roleLists={this.state.roleLists} handleOK={(checkKeys)=>{this.handleSetRoleType(checkKeys)}} handleCancle={this.handleCancleSetRoleType}/>
                </Modal>
                <Modal
                    visible={this.state.showEditModal}
                    title={"添加/编辑用户"}
                    onCancel={this.handleCancleEdit}
                    footer={false}
                    destroyOnClose={true}
                >
                    <EditModal recordInfo={this.state.recordInfo} handleOK={(info)=>{this.handleUpdate(info)}} handleCancle={this.handleCancleEdit}/>
                </Modal>
            </div>
                
            </React.Fragment>
        )
    }
}
