import React, { Component } from 'react';
import { Form, Input, TreeSelect, Button, Row, Col, message } from 'antd';
import { postAction } from '@/axios';

class EditModal extends Component {
    constructor(props){
        super(props);
        this.state = {
            // userTypeList:[],//职务列表信息-编辑/新增弹窗
            departmentList:[],//部门列表信息-编辑/新增弹窗
            recordInfo:{}
        }
    }
    componentDidMount(){
        //获取职务列表信息
        // postAction("/ybt/system/setting/getPositionList").then(res => {
        //     if(res.success){
        //         this.setState({
        //             userTypeList:[{label:"全部",value:""}].concat(res.obj.map(item => ({label:item.positionName,value:item.positionId})))
        //         })
        //     }
        // })
        //获取部门列表
        postAction("/ybt/system/setting/depart/getDepartList").then(res => {
            if(res.success){
                this.setState({
                    departmentList:res.obj
                })
            }
        })
        this.setState({recordInfo:this.props.recordInfo})
    }
    //渲染部门树形菜单
    renderSelectTree = (departmentList = []) => departmentList.map(item => <TreeSelect.TreeNode key={item.departId} value={item.departId} title={item.departName}>{this.renderSelectTree(item.children)}</TreeSelect.TreeNode>);
    //所有输入框改变
    handleInputChange = (e,type) => {
        if(type==="userCode"){
            this.setState({
                recordInfo:{
                    ...this.state.recordInfo,
                    userCode:e.target.value,
                    userName:e.target.value
                }
            });
        }else{
            this.setState({
                recordInfo:{
                    ...this.state.recordInfo,
                    [type]:e.target.value
                }
            });
        }
    }
    //改变职务
    // handleChangePos = (val,option) => {
    //     this.setState({
    //         recordInfo:{
    //             ...this.state.recordInfo,
    //             positionId:val,
    //             positionName:option.props.label
    //         }
    //     })
    // }
    //改变部门
    handleChangeDepart = (val,label=[]) => {
        this.setState({
            recordInfo:{
                ...this.state.recordInfo,
                departId:val,
                departName:label[0]
            }
        })
    }
    //提交
    handleSubmit = () => {
        if(!this.state.recordInfo.name && this.state.recordInfo.name !==0){
            message.warn("请输入姓名");
            return false
        }else if(!this.state.recordInfo.userCode && this.state.recordInfo.userCode !==0){
            message.warn("请输入工号");
            return false
        }
        // else if(!this.state.recordInfo.positionId && this.state.recordInfo.positionId !==0){
        //     message.warn("请选择职务");
        //     return false
        // }
        else if(!this.state.recordInfo.departId && this.state.recordInfo.departId !==0){
            message.warn("请选择部门");
            return false
        }
        this.props.handleOK(this.state.recordInfo);
    }
    //取消
    cancle = () => {
        this.props.handleCancle();
    }
    render() {
        const labelProps = {
            span:5,
            style:{paddingRight:"20px",textAlign:"right",lineHeight:"32px"}
        };
        const contentProps = {
            span:18
        };
        return (
            <React.Fragment>
                <Row style={{marginBottom:"20px"}}>
                    <Col {...labelProps}><i style={{color:"red"}}>*</i>姓名</Col>
                    <Col {...contentProps}>
                        <Input placeholder="请输入姓名" value={this.state.recordInfo.name} onChange={(e) => {this.handleInputChange(e,"name")}}/>
                    </Col>
                </Row>
                <Row style={{marginBottom:"20px"}}>
                    <Col {...labelProps}><i style={{color:"red"}}>*</i>工号</Col>
                    <Col {...contentProps}>
                        <Input placeholder="请输入工号" value={this.state.recordInfo.userCode} onChange={(e) => {this.handleInputChange(e,"userCode")}}/>
                    </Col>
                </Row>
                {/* <Row style={{marginBottom:"20px"}}>
                    <Col {...labelProps}><i style={{color:"red"}}>*</i>职务</Col>
                    <Col {...contentProps}>
                        <Select placeholder="请选择职务" value={this.state.recordInfo.positionId} onChange={this.handleChangePos} style={{width:"100%"}}>
                            {this.state.userTypeList.map(item => <Select.Option key={item.value} label={item.label} value={item.value}>{item.label}</Select.Option>)}
                        </Select>
                    </Col>
                </Row> */}
                <Row style={{marginBottom:"20px"}}>
                    <Col {...labelProps}><i style={{color:"red"}}>*</i>部门</Col>
                    <Col {...contentProps}>
                        <TreeSelect
                            style={{ width: "100%" }}
                            dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                            placeholder= "请选择部门" 
                            allowClear
                            treeDefaultExpandAll
                            value={this.state.recordInfo.departId}
                            onChange={this.handleChangeDepart}
                        >
                            {this.renderSelectTree(this.state.departmentList)}
                        </TreeSelect>
                    </Col>
                </Row>
                <Row style={{marginBottom:"20px"}}>
                    <Col {...labelProps}>手机号</Col>
                    <Col {...contentProps}>
                        <Input placeholder="请输入手机号" value={this.state.recordInfo.phone} onChange={(e) => {this.handleInputChange(e,"phone")}}/>
                    </Col>
                </Row>
                <Row style={{marginBottom:"20px"}}>
                    <Col {...labelProps}>邮箱</Col>
                    <Col {...contentProps}>
                        <Input placeholder="请输入邮箱" value={this.state.recordInfo.email} onChange={(e) => {this.handleInputChange(e,"email")}}/>
                    </Col>
                </Row>
                <Row style={{marginBottom:"20px"}}>
                    <Col span={24} style={{textAlign:"center",marginBottom:"0"}}>
                        <Button type="primary" style={{margin:"0px 16px"}} onClick={this.handleSubmit}> 确认 </Button>
                        <Button onClick={this.cancle}> 取消 </Button>
                    </Col>
                </Row>
            </React.Fragment>
        )
    }
}

export default Form.create()(EditModal);